@use 'variables' as *;
@use 'mixins' as *;

:host {
  display: inline-block;
}

:host([hidden]) {
  display: none;
}

.picker {
  display: flex;
  flex-direction: column;
  min-width: calc(2rem + 7 * 2.5rem);
  max-width: calc(2rem + 7 * 4rem);

  &.picker-weeks {
    min-width: calc(7 * 2.5rem);
    max-width: calc(7 * 4rem);
  }

  &.picker-small {
    min-width: calc(2rem + 7 * 2rem);
    max-width: calc(2rem + 7 * 3rem);

    &.picker-weeks {
      min-width: calc(7 * 2rem);
      max-width: calc(7 * 3rem);
    }
  }
}

.picker-head {
  display: flex;
  gap: 0.5rem;
  align-items: center;

  h3 {
    @include cat-head(4);
    flex: 1;
    text-align: center;
    margin: 0;
  }

  .picker-small & {
    gap: 0.25rem;

    h3 {
      @include cat-head(5);
    }
  }
}

.picker-grid {
  display: grid;
  /* stylelint-disable declaration-block-no-redundant-longhand-properties */
  grid-template-rows: 2rem repeat(6, 1fr);
  grid-template-columns: repeat(7, 1fr);
  grid-template-areas:
    'h h h h h h h'
    'd d d d d d d'
    'd d d d d d d'
    'd d d d d d d'
    'd d d d d d d'
    'd d d d d d d'
    'd d d d d d d';
  /* stylelint-enable declaration-block-no-redundant-longhand-properties */
  margin: 1rem 0 0.5rem;

  .picker-weeks & {
    /* stylelint-disable declaration-block-no-redundant-longhand-properties */
    grid-template-columns: 2rem repeat(7, 1fr);
    grid-template-areas:
      '. h h h h h h h'
      'w d d d d d d d'
      'w d d d d d d d'
      'w d d d d d d d'
      'w d d d d d d d'
      'w d d d d d d d'
      'w d d d d d d d';
    /* stylelint-enable declaration-block-no-redundant-longhand-properties */
  }

  .picker-small & {
    margin: 0.5rem 0;
  }
}

.picker-grid-head {
  grid-area: h;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  place-items: end center;
  padding-bottom: 0.5rem;
}

.picker-grid-weeks {
  grid-area: w;
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  place-items: center right;
  padding-right: 0.5rem;
}

.picker-grid-days {
  grid-area: d;
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  grid-template-columns: repeat(7, 1fr);
}

.picker-grid-head > *,
.picker-grid-weeks > * {
  @include cat-body('xs', 600);
  color: cat-token('color.ui.font.muted') !important;
  text-decoration: none;
}

.date-other {
  opacity: 0.5;
}

.date-disabled {
  opacity: 0.25;
}

.picker-foot {
  display: flex;
  gap: 1rem;
  align-items: center;
  width: min-content;
  min-width: 100%;
  justify-content: space-between;

  .picker-small & {
    gap: 0.5rem;
  }
}

.cursor-help {
  margin: 0;
  @include cat-body('s');
  color: cat-token('color.ui.font.muted') !important;
  text-align: center;
  flex: 1;

  &:first-child {
    text-align: left;
  }

  &:last-child {
    text-align: right;
  }

  &:only-child {
    text-align: center;
  }

  .picker-small & {
    @include cat-body('xs');
  }
}

.cursor-aria {
  @include cat-visually-hidden;
}

.date-range {
  --cat-border-radius-m: 0;
}
